<template>
  <div>
    <vs-button @click="popupActive=true" color="warning" type="filled" icon-pack="feather" icon="icon-filter" icon-after>{{label}}</vs-button>
    <vs-popup class="" title="Pilih" :active.sync="popupActive">

      <vx-input-group class="mb-base" prependClasses="border border-solid border-grey border-r-0">
        <template slot="prepend">
          <div class="prepend-text">
            <vs-checkbox v-model="selectAll" ><span class="text-primary"></span></vs-checkbox>
          </div>
        </template>
        <vs-input class="inputx" placeholder="Search" v-model="search" />
      </vx-input-group>
      
      <ul class="centerx">
        <li v-for="(tr, index) in filteredItems" :key="index" class="p-2">
          <vs-checkbox v-model="itemSelected" :vs-value="tr" >{{ tr.nama }}</vs-checkbox>
        </li>
      </ul>
    </vs-popup>
  </div>
</template>

<script>
export default {
  props:
  {
    value:Array,
    label:{
      type:String,
      default:'Pilih'
    },
    arrayList:{
      type:Array,
      default: function () {
        return []
      }
    }

  }
  ,
  data() {
    return {
      popupActive: false,
      itemSelected:[],
      itemFiltered:[],
      search:'',
      items: []
    }
  },
  mounted() {
    this.items = this.arrayList;
    // console.log(this.items)
  },
  watch: {
    itemSelected: {
      deep: true,
      handler(value) {
        this.$emit('input', value);
      }
    },
  },
  computed:{
    selectAll: {
      get () {
        return this.itemSelected.length === this.arrayList.length
      },
      set (value) {
        this.itemSelected = []

        if (value) {
          this.arrayList.forEach((select) => {
            this.itemSelected.push(select)
          })
        }
      }
    }, // END selectAll
    filteredItems() {
      return this.arrayList.filter(item => {
        if(item.nama == undefined)
          return false
        return item.nama.toLowerCase().indexOf(this.search.toLowerCase()) > -1
      })
    }
  },
  
}
</script>