<script>
import Datepicker from 'vuejs-datepicker'
import vcheckboxtoko from '../../components/CheckboxToko'

export default {
    data() {
        return{
            uri :'data:application/vnd.ms-excel;base64,',
            template:'<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head><body><table>{table}</table></body></html>',
            base64: function(s){ return window.btoa(unescape(encodeURIComponent(s))) },
            format: function(s, c) { return s.replace(/{(\w+)}/g, function(m, p) { return c[p]; }) },
            // Select Option 1
            tokoSelected:null,
            // Select Tanggal
            monthSelected: null,
            // Toko
            toko:[],
            // Excel
            excellStore: true,
            // Table Stok
            stok:[
                {
                    'deskripsi': 'AYAM',
                    'saldo_awal': 200,
                    'barang_masuk': 100,
                    'sales': 50,
                    'waste': 1,
                    'saldo_akhir': 249,
                    'gerai': '-',
                    'as_am': 249,
                    'selisih_qty': 249,
                    'selisih_rp': 0,
                    'saldo': 249
                },
                {
                    'deskripsi': 'AYAM',
                    'saldo_awal': 200,
                    'barang_masuk': 100,
                    'sales': 50,
                    'waste': 1,
                    'saldo_akhir': 249,
                    'gerai': '-',
                    'as_am': 249,
                    'selisih_qty': 249,
                    'selisih_rp': 0,
                    'saldo': 249
                }
            ],
            // List Toko
            listToko:[],
            sort: {
                key: '',
                isAsc: false
            },
        }
    },

    components:{
        Datepicker,
        vcheckboxtoko
    },

    computed: {
        validateFormStore() {
            return !this.errors.any() && this.tokoSelected != null && this.tanggalSelectedStore != null
        },
        validateFormAccurate() {
            return !this.errors.any() && this.tanggalSelectedAccurate != null
        },
        selectAll: {
            get () {
                return this.selected.length === this.listToko.length
            },
            set (value) {
                this.selected = []

                if (value) {
                this.listToko.forEach((select) => {
                    this.selected.push(select.kode)
                })
                }
            }
        },
        filteredItems(){
            return this.listToko.filter(item => {
                if(item.gerai == undefined)
                return false
                return item.gerai.toLowerCase().indexOf(this.search.toLowerCase()) > -1
            })
        },
        sortedItems (){
            const list = this.accurate.slice();
            const sortList = !!this.sort.key
            if (sortList) {
                list.sort((a, b) => {
                a = a[this.sort.key]
                b = b[this.sort.key]

                return (a === b ? 0 : a > b ? 1 : -1) * (this.sort.isAsc ? 1 : -1)
                });
            }
            
            return list;
        }
    },

    // mounted(){
    //     this.loadAllToko();
    //     this.listAllToko();
    // },

    methods:{
        tableToExcelStore(table, name){
            if (!table.nodeType) table = this.$refs.tableStore
            var ctx = {worksheet: name || 'Worksheet', table: table.innerHTML}
            window.location.href = this.uri + this.base64(this.format(this.template, ctx))
        },

        sortedClass (key) {
            return this.sort.key === key ? `sorted ${this.sort.isAsc ? 'asc' : 'desc' }` : '';
        },

        sortBy (key) {
            this.sort.isAsc = this.sort.key === key ? !this.sort.isAsc : false;
            this.sort.key = key;
        },


    }
}
</script>

<style scoped>

/* CSS Table Store */
.table-wrapper-store{
