    position: sticky;
    top: 0;
    background: #7367f0;
    color: #fff;
    border: solid 1px #DDEFEF;
    padding: 5px;
    white-space: nowrap;
    width: 370px
}

.fixed-column thead th:first-child {
    left: 0;
    z-index: 1;
}

.fixed-column tbody th {
    position: -webkit-sticky; /* for Safari */
    position: sticky;
    left: 0;
    padding: 5px;
    white-space: nowrap;
    border: solid 1px #DDEFEF;
    background: #fff;
    color: #797777;
    text-align:left;
}

/* CSS Table Accurate */
.table-wrapper {
  overflow-y: scroll;
  height: auto;
}

.table-wrapper th {
    position: sticky;
    top: 0;
    z-index: 1;
}

table {
  border-collapse: collapse;
  width: 100%;
}

th {
    font-size: 12px;
    background: #7367f0;
    color: #fff;
    border: solid 1px #DDEFEF;
    padding: 5px;
    text-align: center;
    white-space: nowrap;
}

table th.sorted.asc::after {
	display: inline-block;
	content: ' ▼';
}

 table th.sorted.desc::after {
	display: inline-block;
	content: ' ▲';
}

td,th{
  padding: 5px;
  white-space: nowrap;
  border: solid 1px #DDEFEF;
}
</style>